import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseRouteReuseStrategy } from '@angular/router';
import { Observable } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class GeneralService {
    private baseUrl: string = "http://localhost:3000/api/v1.1/";

    constructor(private httpClient: HttpClient) { }

    newsletterSubscribe(data): Observable<{}> {
        return this.httpClient.post(this.baseUrl+'newsletter-add' , data, {observe: 'response'});
    }
    bookAppointment(data) {
        return this.httpClient.post(this.baseUrl+'appointment-add', data);
    }

}
